import React from 'react'
import Card from '../components/Card'
import { Instruments } from '../models/Instruments'

const BassScreen = () => {

    const basses = Instruments.filter((instrument) => instrument.type === "b")
    
    return (
        <div className="container mt-3">
            <h1>Basses Screen</h1>
            <hr />

            <div className="row">
                {basses.map((bass) => (
                    <Card key={bass.id} {...bass} />
                ))}
            </div>
        </div>
    )
}

export default BassScreen